:root {
  --main-color: #8dc6f2;
  --edit: rgba(212, 160, 92, .647);
  --done: rgba(86, 179, 70, .627);
  --delete: rgba(241, 30, 30, .663);
}

body {
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

h1, h2, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav {
  margin-bottom: 50px;
}

.container {
  width: 1200px;
  outline: 1px solid green;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.btn {
  width: 100px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  padding: 10px 5px;
  font-family: Roboto Mono, monospace;
  font-size: 16px;
  font-weight: 500;
}

.nav__list {
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.nav__btn {
  background-color: var(--main-color);
  color: #fff;
}

.todo__title {
  text-align: center;
  margin-bottom: 20px;
  font-family: Dancing Script, cursive;
  font-size: 36px;
}

.todo__form {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 50px;
  display: flex;
}

.todo__field {
  width: 40%;
  border: 1px solid var(--main-color);
  color: var(--main-color);
  border-radius: 20px;
  padding: 15px;
  font-size: 18px;
}

.todo__field::-ms-input-placeholder {
  color: rgba(141, 198, 242, .616);
}

.todo__field::placeholder {
  color: rgba(141, 198, 242, .616);
}

.todo__field:focus {
  outline: none;
}

.todo__submit {
  border: 1px solid var(--main-color);
  text-transform: uppercase;
  color: var(--main-color);
  background-color: rgba(0, 0, 0, 0);
  padding: 15px 10px;
  font-size: 18px;
}

.todo__item {
  width: 40%;
  border: 1px solid var(--main-color);
  border-radius: 20px;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  font-size: 18px;
  display: flex;
}

.todo__btn-item {
  width: 50px;
  text-transform: lowercase;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 10px;
  padding: 0 5px;
  font-family: Roboto Mono, monospace;
  font-size: 12px;
}

.todo__edit {
  color: var(--edit);
}

.todo__done {
  color: var(--done);
}

.todo__delete {
  color: var(--delete);
}

